"use client";

import { useState } from "react";
import { useWodComments } from "./WodCommentsStore";

export default function WodComments({ date }: { date: string }) {
  const { commentsByDate, addComment } = useWodComments();
  const [text, setText] = useState("");

  const comments = commentsByDate[date] ?? [];

  return (
    <section className="rounded-2xl border border-gray-200 bg-white p-4 shadow-sm dark:border-gray-800 dark:bg-gray-900">
      <div className="text-sm font-semibold text-gray-800 dark:text-white/90">
        Comentarios ({comments.length})
      </div>

      <div className="mt-2 space-y-2">
        {comments.map((c) => (
          <div
            key={c.id}
            className="rounded-lg bg-gray-50 px-3 py-2 text-sm text-gray-800 dark:bg-gray-800/60 dark:text-white/90"
          >
            {c.text}
          </div>
        ))}

        {comments.length === 0 && (
          <div className="text-sm text-gray-500 dark:text-gray-400">
            SÃ© el primero en comentar ðŸ’¬
          </div>
        )}
      </div>

      <form
        className="mt-3 flex gap-2"
        onSubmit={(e) => {
          e.preventDefault();
          if (!text.trim()) return;
          addComment(date, text.trim());
          setText("");
        }}
      >
        <input
          value={text}
          onChange={(e) => setText(e.target.value)}
          placeholder="EscribÃ­ un comentarioâ€¦"
          className="flex-1 rounded-xl border border-gray-300 bg-white px-3 py-2 text-sm text-gray-800 placeholder:text-gray-400 dark:border-gray-700 dark:bg-gray-900 dark:text-white/90 dark:placeholder:text-gray-500"
        />
        <button className="rounded-xl bg-gray-900 px-4 py-2 text-sm text-white dark:bg-white dark:text-gray-900">
          Enviar
        </button>
      </form>
    </section>
  );
}
