"use client";

import { useMemo, useState } from "react";
import { loadWods } from "@/lib/wodStore";
import WodSection from "@/components/athlete/WodSection";
import WodComments from "@/components/athlete/WodComments";

export default function WodPage() {
  const wod = loadWods()[0];
  if (!wod) {
    return <div>No hay WOD publicado.</div>;
  }
  
  const [mode, setMode] = useState<"rx" | "scaled">("rx");

  const dateLabel = useMemo(() => {
    const d = new Date(wod.date + "T00:00:00");
    return d.toLocaleDateString("es-ES", {
      weekday: "long",
      day: "2-digit",
      month: "2-digit",
    });
  }, [wod.date]);

  return (
    <div>
      <div className="flex items-start justify-between gap-3">
        <div>
          <h1 className="text-xl font-semibold">WOD</h1>

          <div className="mt-1 text-sm text-gray-500">
            {dateLabel} â€¢ {wod.title}
          </div>
        </div>

        <a
          href="/athlete/wod/historico"
          className="text-sm text-gray-600 underline"
        >
          HistÃ³rico
        </a>

        <div className="rounded-xl border border-gray-200 bg-white p-1 text-sm">
          <button
            onClick={() => setMode("rx")}
            className={`rounded-lg px-3 py-1 ${mode === "rx"
                ? "bg-gray-900 text-white"
                : "text-gray-700"
              }`}
          >
            RX
          </button>
          <button
            onClick={() => setMode("scaled")}
            className={`rounded-lg px-3 py-1 ${mode === "scaled"
                ? "bg-gray-900 text-white"
                : "text-gray-700"
              }`}
          >
            Scaled
          </button>
        </div>
      </div>

      <div className="mt-3 rounded-2xl border border-gray-200 bg-white p-4 shadow-sm">
        <div className="text-sm font-semibold">IntenciÃ³n</div>
        <p className="mt-1 text-sm text-gray-700">{wod.intent}</p>
      </div>

      <div className="mt-3 grid gap-3">
        <WodSection title="Warm-up" items={wod.warmup} />
        {wod.strength && (
          <WodSection title="Strength" items={wod.strength} />
        )}
        <WodSection title="Metcon" items={wod.metcon} />
        <WodSection
          title={mode === "rx" ? "Standards (RX)" : "Opciones (Scaled)"}
          items={mode === "rx" ? wod.rx : wod.scaled}
        />
      </div>

      <div className="mt-4">
        <WodComments date={wod.date} />
      </div>
    </div>
  );
}
